*,
*::after,
*::before {
  border: 0;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

@import url("https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,400;0,700;1,400;1,700&family=Zen+Kurenaido&display=swap");

//colors

$primary: #7faff7;
$success: #419d5e;
$danger: #f64747;
$dark: #121614;
$light: #cbd2dc;

// box shadow

$boxs: 3px 5px 8px 2px;

// transition
$transition: all 250ms ease-out;

.modal-container {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  display: grid;
  place-items: center;
  background-color: rgba($dark, 0.2);
  opacity: 0;
  pointer-events: none;
  transition: $transition;

  &.active {
    opacity: 1;
    pointer-events: all;
  }

  .modal {
    max-width: 700px;
    max-height: 80vh;
    overflow: auto;
    background-color: #eeeeee;
    border: 2px solid $dark;
    padding: 2rem;
    position: relative;

    &__title {
      font-size: 2rem;
      line-height: 1.2;
      margin-bottom: 1rem;
      max-width: 85%;
    }

    &__btn {
      position: absolute;
      top: 0.5rem;
      right: 0.5rem;
      color: $dark;
      font-size: 1.3rem;
      line-height: 1.3rem;
      height: 30px;
      width: 30px;
      cursor: pointer;
      border: 2px solid $dark;
      border-radius: 59%;
      background-color: #f0f0f0;
      box-shadow: $boxs rgba($dark, 0.2);
      transition: $transition;

      &:focus,
      &:hover {
        // outline: none;
        border: 2px solid $light;
        color: $light;
        box-shadow: none;
      }
    }
  }
}

body {
  font-family: "Lato", sans-serif;
  display: grid;
  place-items: end center;
  padding: 0.7rem;
  color: $dark;
}

input,
textarea,
select,
button {
  font-family: "Zen Kurenaido", serif;
}

.form-container {
  max-width: 1000px;
  margin: 1rem auto;

  h1 {
    font-size: 3rem;
    text-align: center;
  }

  h3 {
    color: $success;
    text-align: center;
  }

  .message {
    text-align: center;
    margin: 1rem 0 1.5rem;
    padding: 0.7rem;
    color: white;
    border-radius: 0.3rem;

    &.success-message {
      background-color: $success;
      box-shadow: 2px 4px 7px 2px rgba($success, 0.2);
    }
    &.remove-message {
      background-color: $primary;
      box-shadow: 2px 4px 7px 2px rgba($primary, 0.2);
    }
    &.alert-message {
      background-color: $danger;
      box-shadow: 2px 4px 7px 2px rgba($danger, 0.2);
    }
  }
}

form {
  display: grid;
  grid-gap: 1rem;
  margin: 1rem 0;
  width: 100%;

  label,
  input,
  textarea {
    font-size: inherit;
  }

  [for="title"],
  [for="note"] {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    padding: 1rem;
    grid-gap: 1.5rem;
    background-color: rgba($light, 0.2);
    font-weight: bold;

    input,
    textarea {
      flex: 1 1 80%;
      border: 2px solid $light;
      padding: 0.5rem;
      color: $dark;
      width: 100%;

      &:focus {
        outline: 2px solid $dark;
      }
    }
  }

  [type="submit"] {
    background-color: $success;
    box-shadow: $boxs rgba($success, 0.2);
    border: 3px solid transparent;
    font-size: 1.3rem;
    color: white;
    padding: 0.5re;
    cursor: pointer;
    transition: all 250ms ease-in-out;

    &:hover,
    &:focus {
      outline: none;
      box-shadow: none;
      border: 3px solid rgba($dark, 0.5);
    }
  }
}

.note-container {
  // max-width: 1000px;
  margin: 2rem auto 1rem;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  grid-gap: 1.5rem;

  .note {
    border: 3px solid rgba($primary, 0.2);
    padding: 1.5rem;
    transition: all 250ms ease-out;
    &__title {
      font-size: 2rem;
      margin-bottom: 1.5rem;
    }

    &__body {
      overflow: hidden;
      max-height: 8rem;
      -webkit-box-orient: vertical;
      display: -webkit-box;
      text-overflow: ellipsis;
      -webkit-line-clamp: 4;
    }

    &__btns {
      display: grid;
      grid-template-columns: auto auto;
      grid-gap: 0.5rem;
      .note__btn {
        // font-family: inherit;

        font-size: inherit;
        padding: 0.5rem 1rem;
        margin-top: 1rem;
        color: wheat;
        cursor: pointer;
        transition: $transition;

        &:focus,
        &:hover {
          box-shadow: none;
        }
      }
      .note__view {
        background-color: $primary;
        box-shadow: $boxs rgba($primary, 0.2);
      }
      .note__delete {
        background-color: $danger;
        box-shadow: $boxs rgba($danger, 0.2);
      }
    }
  }
}
